
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import loginArigin from '@/components/loginArigin.vue'
import {sendSMSCode, applyPointMoney , getApplyPointMoneyInfo} from '@/api/user.js'

export default {
name: 'loginIndex',
components: {
XInput,
Group,
XButton,
loginArigin
},
data () {
return {
getCodeStatus: false,
countdown: 60,
postData: {
mob:'',
name:'',
idnumber:'',
bankname:'',
banknumber:'',
bankaccount:'',
}
}
},
created(){
this.postData.point = this.$route.query.point;
this.postData.code = "";

getApplyPointMoneyInfo()
.then(res => {
if (res.data.errorcode === 0) {
this.postData.mob = res.data.data.mob
this.postData.name = res.data.data.name
this.postData.idnumber = res.data.data.idnumber
this.postData.bankname = res.data.data.bankname
this.postData.banknumber = res.data.data.banknumber
this.postData.bankaccount = res.data.data.bankaccount
} else {
this.$vux.toast.text(res.data.errormsg, 'middle')
}
})
.catch(err => {
this.$vux.toast.text('网络异常！', 'middle')
})

},
methods: {

sendSMSCode () {
if (this.getCodeStatus) {

return
}




const reg = 11 && /^((13|14|15|17|18)[0-9]{1}\d{8})$/
if (!reg.test(this.postData.mob)) {
if (this.postData.mob === '' || this.postData.mob === null) {
this.$vux.toast.text('请填写手机号', 'middle')
return
}
this.$vux.toast.text('手机号有误')
return
}
const postData = {
mob: this.postData.mob,
type: 1 
}
const _this = this
sendSMSCode(postData)
.then(res => {
if (res.data.errorcode === 0) {

_this.$vux.toast.text('已发送,请注意查收！', 'middle')
_this.getCodeStatus = true
let interVal = setInterval(function () {
if (_this.getCodeStatus && _this.countdown > 0) {
_this.countdown--
} else {
_this.getCodeStatus = false
clearInterval(interVal)
_this.countdown = 60
}
}, 1000)
} else {
this.$vux.toast.text(res.data.errormsg, 'middle')
}
})
.catch(err => {
console.log(err)
this.$vux.toast.text('网络异常！', 'middle')
})
},

handleSubmit () {
console.log(this.postData)

if (this.postData.name === '')
{
this.$vux.toast.text('请输入您的姓名')
return
}

const reg1 = /(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/
if (!reg1.test(this.postData.idnumber)) {
this.$vux.toast.text('身份证输入不合法')
return
}
const reg2 = /^\d{1,}$/
if (!reg2.test(this.postData.banknumber)) {
this.$vux.toast.text('银行账号有误')
return
}
const reg3 = 11 && /^((13|14|15|17|18)[0-9]{1}\d{8})$/
if (!reg3.test(this.postData.mob)) {
this.$vux.toast.text('手机号有误')
return
}
if (this.postData.banknumber === '')
{
this.$vux.toast.text('请输入您的收款银行名称')
return
}
if (this.postData.bankaccount === '')
{
this.$vux.toast.text('请输入您的银行开户行名称')
return
}

if (this.postData.code === '')
{
this.$vux.toast.text('请输入手机验证码')
return
}





if (this.postData.mob === '' || this.postData.name === '' || this.postData.idnumber === '' || this.postData.banknumber === '' || this.postData.bankname === '' || this.postData.code === ''|| this.postData.bankaccount === '' ) {
this.$vux.toast.text('请填写完整信息')
return
}
this.$vux.loading.show()


applyPointMoney(this.postData)
.then(res => {
this.$vux.loading.hide()
if (res.data.errorcode === 0) {

this.$vux.toast.text('申请提现成功，请等待处理', 'middle')
this.$router.push({path: '/myIntegral'})
} else {
this.$vux.toast.text(res.data.errormsg, 'middle')
}
})
.catch(err => {
this.$vux.loading.hide()
console.log(err)
this.$vux.toast.text('网络异常！', 'middle')
})
}
}
}



